.loader-wrapper {
  display: flex;
  justify-content: center;
}

.loader {
  width: 60px;
  height: 60px;
  border: 5px solid var(--dark-blue);
  border-radius: 50%;
  display: inline-block;
  box-sizing: border-box;
  position: relative;
  animation: pulse 1s linear infinite;
  margin-top: 5em;
}
.loader:after {
  content: '';
  position: absolute;
  width: 60px;
  height: 60px;
  border: 5px solid var(--dark-blue);
  border-radius: 50%;
  display: inline-block;
  box-sizing: border-box;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  animation: scaleUp 1s linear infinite;
}

@keyframes scaleUp {
  0% {
    transform: translate(-50%, -50%) scale(0);
  }
  60%,
  100% {
    transform: translate(-50%, -50%) scale(1);
  }
}
@keyframes pulse {
  0%,
  60%,
  100% {
    transform: scale(1);
  }
  80% {
    transform: scale(1.2);
  }
}
